import { Speaker, Visit, WorkloadBalance } from '@/types';
import { parseISO, isAfter, subMonths } from 'date-fns';

// Facteurs de pond√©ration


// Seuil de visite critique (max par mois pour ne pas surcharger)
const MAX_VISITS_PER_MONTH = 2;

export const calculateWorkload = (
  speaker: Speaker, 
  visits: Visit[], 
  currentDate: Date = new Date()
): WorkloadBalance => {
  // On regarde les 6 derniers mois
  const sixMonthsAgo = subMonths(currentDate, 6);
  
  // Filtrer les visites pass√©es de cet orateur
  const speakerVisits = visits.filter(v => 
    v.id === speaker.id && 
    (v.status === 'completed' || v.status === 'confirmed') &&
    isAfter(parseISO(v.visitDate), sixMonthsAgo)
  );

  // 1. Calcul de la charge actuelle (%)
  // Bas√© sur la fr√©quence r√©cente : si > 1 visite/mois = charge √©lev√©e
  const visitsPerMonth = speakerVisits.length / 6;
  const rawLoad = (visitsPerMonth / MAX_VISITS_PER_MONTH) * 100;
  const currentLoad = Math.min(Math.round(rawLoad), 100);

  // 2. Score de charge (1-5)
  let workloadScore = 1;
  if (currentLoad > 80) workloadScore = 5;
  else if (currentLoad > 60) workloadScore = 4;
  else if (currentLoad > 40) workloadScore = 3;
  else if (currentLoad > 20) workloadScore = 2;

  // 3. Derni√®re visite
  const sortedVisits = [...speakerVisits].sort((a, b) => 
    new Date(b.visitDate).getTime() - new Date(a.visitDate).getTime()
  );
  const lastVisit = sortedVisits.length > 0 ? sortedVisits[0].visitDate : null;

  // 4. Estimation temps trajet (Simul√© ici, id√©alement viendrait de Google Maps API)
  // On pourrait utiliser la localisation de la congr√©gation vs domicile orateur
  const travelTime = 30; // Valeur par d√©faut

  return {
    speakerId: speaker.id,
    speakerName: speaker.nom,
    currentLoad,
    maxCapacity: MAX_VISITS_PER_MONTH * 100, // Capacit√© th√©orique relative pour l'affichage
    lastVisit,
    travelTime,
    workloadScore,
    availabilityNextMonth: currentLoad < 80 // Si < 80% charg√©, dispo
  };
};

export const getWorkloadColor = (score: number): string => {
  switch (score) {
    case 5: return 'bg-red-500 text-white';
    case 4: return 'bg-orange-500 text-white';
    case 3: return 'bg-yellow-500 text-white';
    case 2: return 'bg-blue-500 text-white';
    default: return 'bg-green-500 text-white';
  }
};

export const getWorkloadLabel = (score: number): string => {
  switch (score) {
    case 5: return 'Surcharg√©';
    case 4: return 'Tr√®s occup√©';
    case 3: return 'Occup√©';
    case 2: return 'Mod√©r√©';
    default: return 'Disponible';
  }
};
