import React, { useMemo } from 'react';
import { Card, CardBody, CardHeader } from '@/components/ui/Card';
import { Visit } from '@/types';
import { CreditCard, TrendingUp, DollarSign } from 'lucide-react';

interface FinancialDashboardProps {
  visits: Visit[];
}

export const FinancialDashboard: React.FC<FinancialDashboardProps> = ({ visits }) => {
  const stats = useMemo(() => {
    const visitsWithExpenses = visits.filter(v => v.expenses && v.expenses.length > 0);
    const totalExpenses = visits.reduce((sum, v) => {
      const visitTotal = (v.expenses || []).reduce((s, e) => s + e.amount, 0);
      return sum + visitTotal;
    }, 0);
    const avgPerVisit = visitsWithExpenses.length > 0 ? totalExpenses / visitsWithExpenses.length : 0;

    return {
      total: totalExpenses,
      visitsWithExpenses: visitsWithExpenses.length,
      average: avgPerVisit,
    };
  }, [visits]);

  return (
    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
      <Card>
        <CardHeader>
          <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
            <DollarSign className="w-4 h-4" />
            Total des d√©penses
          </h3>
        </CardHeader>
        <CardBody>
          <p className="text-2xl font-bold text-gray-900 dark:text-white">
            {stats.total.toFixed(2)} ‚Ç¨
          </p>
        </CardBody>
      </Card>

      <Card>
        <CardHeader>
          <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
            <CreditCard className="w-4 h-4" />
            Visites avec d√©penses
          </h3>
        </CardHeader>
        <CardBody>
          <p className="text-2xl font-bold text-gray-900 dark:text-white">
            {stats.visitsWithExpenses}
          </p>
        </CardBody>
      </Card>

      <Card>
        <CardHeader>
          <h3 className="text-sm font-medium text-gray-500 dark:text-gray-400 flex items-center gap-2">
            <TrendingUp className="w-4 h-4" />
            Moyenne par visite
          </h3>
        </CardHeader>
        <CardBody>
          <p className="text-2xl font-bold text-gray-900 dark:text-white">
            {stats.average.toFixed(2)} ‚Ç¨
          </p>
        </CardBody>
      </Card>
    </div>
  );
};
